@font-face {
  font-family: 'VT323';
  font-style: normal;
  font-weight: 400;
  src: local('VT323 Regular'), local('VT323-Regular'), url("fonts/VT323-latin.woff2") format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2212, U+2215;
}
.VT323,
.Editor .CodeMirror,
body {
  font-family: 'VT323', monospace;
}
.cm-s-nyx8.CodeMirror {
  background: #08141e;
  color: #4e495f;
}
.cm-s-nyx8 div.CodeMirror-selected {
  background: #20394f;
}
.cm-s-nyx8 .CodeMirror-line::selection,
.cm-s-nyx8 .CodeMirror-line > span::selection,
.cm-s-nyx8 .CodeMirror-line > span > span::selection {
  background: #20394f;
}
.cm-s-nyx8 .CodeMirror-line::-moz-selection,
.cm-s-nyx8 .CodeMirror-line > span::-moz-selection,
.cm-s-nyx8 .CodeMirror-line > span > span::-moz-selection {
  background: #20394f;
}
.cm-s-nyx8 .CodeMirror-gutters {
  background: #08141e;
  border: none;
}
.cm-s-nyx8 .CodeMirror-guttermarker {
  color: #816271;
}
.cm-s-nyx8 .CodeMirror-guttermarker-subtle {
  color: #0f2a3f;
}
.cm-s-nyx8 .CodeMirror-linenumber {
  color: #0f2a3f;
}
.cm-s-nyx8 .CodeMirror-cursors {
  z-index: 0;
}
.cm-s-nyx8 .CodeMirror-cursor {
  border-left: 0.4em solid #20394f;
  z-index: 0;
}
.cm-s-nyx8 span.cm-comment {
  color: #4e495f;
}
.cm-s-nyx8 span.cm-atom {
  color: #f6d6bd;
}
.cm-s-nyx8 span.cm-number {
  color: #f6d6bd;
}
.cm-s-nyx8 span.cm-property,
.cm-s-nyx8 span.cm-attribute {
  color: #c3a38a;
}
.cm-s-nyx8 span.cm-keyword {
  color: #816271;
}
.cm-s-nyx8 span.cm-string {
  color: #816271;
}
.cm-s-nyx8 span.cm-variable {
  color: #c3a38a;
}
.cm-s-nyx8 span.cm-variable-2 {
  color: #997577;
}
.cm-s-nyx8 span.cm-def {
  color: #f6d6bd;
}
.cm-s-nyx8 span.cm-bracket {
  color: #4e495f;
}
.cm-s-nyx8 span.cm-tag {
  color: #816271;
}
.cm-s-nyx8 span.cm-link {
  color: #f6d6bd;
}
.cm-s-nyx8 span.cm-error {
  background: #816271;
  color: #20394f;
}
.cm-s-nyx8 .CodeMirror-activeline-background {
  background: #f00;
}
.cm-s-nyx8 .CodeMirror-matchingbracket {
  text-decoration: underline;
  color: #f00 !important;
}
.cm-s-nyx8 span.cm-searching.cm-overlay {
  border-bottom: solid #f6d6bd 2px;
}
.Code {
  position: absolute;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-rows: 1.5rem auto;
  grid-template-columns: auto calc(128px * 3 + 3rem);
}
.Code nav {
  grid-column: 1/3;
}
.Code .Output {
  margin: 1.5rem auto 0 auto;
}
.Code .Output canvas {
  margin-bottom: 1.5rem;
}
.Editor {
  position: relative;
}
.Editor .CodeMirror {
  position: absolute;
  height: 100%;
  width: 100%;
  border-right: solid #0f2a3f 2px;
  font-size: 1.25rem;
}
.Home nav {
  margin-bottom: 3rem;
}
.Home h1 {
  visibility: hidden;
  position: absolute;
}
.Home .logo-and-timestamp {
  display: grid;
  grid-template-columns: 50% 50%;
  grid-row-gap: 0.75rem;
  width: calc(100vw - 3rem);
  max-width: 46rem;
  margin: 0 auto 3rem auto;
}
.Home .logo-and-timestamp img {
  width: 100%;
  display: inline-block;
  grid-column: 1/3;
  image-rendering: pixelated;
}
.Home .logo-and-timestamp span {
  font-size: 1rem;
  color: #997577;
}
.Home .logo-and-timestamp span:nth-child(3) {
  text-align: right;
}
.Home p {
  width: calc(100vw - 3rem);
  max-width: 46rem;
  margin: 3rem auto;
  color: #997577;
}
.Home p.thumbnails {
  margin: 0 auto;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.Home p.thumbnails img {
  width: calc(128px - 8px);
  padding: 0.75rem 4px;
  image-rendering: pixelated;
}
.Home p.feature {
  display: grid;
  grid-template-columns: auto 30rem;
  grid-column-gap: 1.5rem;
  margin-bottom: 6rem;
}
@media screen and (max-width: 44rem) {
  .Home p.feature {
    grid-template-columns: 100%;
    grid-column-gap: 0;
    grid-row-gap: 1.5rem;
  }
}
.Home p.feature span {
  font-size: 1.5rem;
}
.Home p.feature span:before {
  content: 'â–ª ';
}
.Home p.feature img {
  width: 100%;
  image-rendering: pixelated;
}
@media screen and (max-width: 44rem) {
  .Home p.feature img {
    max-width: 32rem;
    margin: 0 auto;
  }
}
.Output {
  width: calc(128px * 3);
}
.Output canvas {
  display: block;
  width: 100%;
  height: calc(128px * 3);
  image-rendering: pixelated;
}
.Run .Output {
  margin: 3rem auto 0 auto;
}
.Run .Output canvas {
  margin-bottom: 1.5rem;
}
html {
  background: #08141e;
}
body {
  margin: 0;
  padding: 0;
  color: #f6d6bd;
  font-size: 1.5rem;
}
p {
  margin: 0;
  padding: 0;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
a {
  color: #f6d6bd;
}
a:hover {
  background: #f6d6bd;
  color: #08141e;
}
nav {
  border-bottom: solid #0f2a3f 2px;
  display: flex;
  justify-content: space-between;
}
nav ul {
  display: flex;
}
nav a {
  text-decoration: none;
  text-transform: uppercase;
  padding: 0 0.5rem;
  color: #997577;
}
nav a:hover {
  color: #f6d6bd;
  background: #08141e;
}
nav a.active {
  color: #c3a38a;
  background: #0f2a3f;
  pointer-events: none;
  cursor: default;
}
